import React from "react"
import ApplicationsData from "./ApplicationsData"
import Application from "./Application"

const Portfolio = () => {
    
    const ApplicationList = ApplicationsData.map(Ap => <Application key={Ap.name} name={Ap.name} url={Ap.url} description={Ap.description} HTML={Ap.HTML} CSS={Ap.CSS} JavaScript={Ap.JavaScript} Bootstrap={Ap.Bootstrap} Sass={Ap.Sass} ReactJS={Ap.ReactJS} Redux={Ap.Redux} Wide={Ap.Wide} />)
    
    return (
        <section className="container">
            <h1>Welcome to my Portfolio</h1>
            <p>I will be keeping most of my Programming work here on this site, but you can always check it also at my <a className="download-cv" href="https://github.com/dan-sprucek" target="_blank" rel="noopener noreferrer">GITHUB PROFILE</a>.</p>
            <div className="d-flex flex-column">
                {ApplicationList}
            </div>
        </section>
    )
}

export default Portfolio