import React from "react"

const Contact = (props) => {
    return (
        <section className="container">
            <h1>Contact me!</h1>
            <p>I love people! Almost as much as computers!</p>
            <p>Feel free to use any of the ways below:</p>
            <div className="d-flex flex-column">
                <div className="contact-container">
                    <h3>E-mail</h3>
                    <span><a href="mailto:dan.sprucek@gmail.com">dan.sprucek@gmail.com</a> <i className="far fa-copy" data-value="dan.sprucek@gmail.com" onClick={props.copyToClipboard} title="Copy to clipboard"></i></span>
                </div>
                <div className="contact-container">
                    <h3>LinkedIn</h3>
                    <span><a href="https://www.linkedin.com/in/dansprucek/" target="_blank" rel="noopener noreferrer">https://www.linkedin.com/in/dansprucek/</a> <i className="far fa-copy" data-value="https://www.linkedin.com/in/dansprucek/" onClick={props.copyToClipboard} title="Copy to clipboard"></i></span>
                </div>
                <div className="contact-container">
                    <h3>Skype</h3>
                    <span>dan.sprucek <i className="far fa-copy" data-value="dan.sprucek" onClick={props.copyToClipboard} title="Copy to clipboard"></i></span>
                </div>
                <div className="contact-container">
                    <h3>Facebook</h3>
                    <span><a href="https://www.facebook.com/dan.sprucek" target="_blank" rel="noopener noreferrer">https://www.facebook.com/dan.sprucek</a> <i className="far fa-copy" data-value="https://www.facebook.com/dan.sprucek" onClick={props.copyToClipboard} title="Copy to clipboard"></i></span>
                </div>

            </div>
        </section>
    )
}

export default Contact