import React from "react"

const About = () => {

    const handleScroll = (event) => {
        let scrollTo = event.currentTarget.dataset.value
        document.getElementById(scrollTo).scrollIntoView({behavior: "smooth"})
    }

    return (
        <section className="container">
            <h1>Basic Information</h1>
            <div className="subsection-container">
                <div className="subsection-container-50PER d-flex flex-column justify-content-between align-items-center">
                    <img id="photo-dan" src="./sources/CV.jpg" alt="Dan Sprucek"></img>
                    <a className="download-cv" href="https://drive.google.com/file/d/1ZcU5-H9zAH93qZ5IUYBiu321HDW24axe/view?usp=sharing" target="_blank" rel="noopener noreferrer">DOWNLOAD CV</a>
                </div>
                <div className="subsection-container-50PER">
                    <dl className="mb-0">
                        <dt>Name</dt>
                        <dd>Dan Sprucek</dd>
                        <dt>Age</dt>
                        <dd>32</dd>
                        <dt>Likes</dt>
                        <dd>Coding, Sport, Board Games</dd>
                        <dt>Humor</dt>
                        <dd className="mb-0">Infinite</dd>
                    </dl>
                </div>
            </div>
            <div>
                <h2>Would you like to know more?</h2>
                <div className="d-flex flex-column">
                    <div className="d-flex justify-content-center">
                        <span className="about-scroll" onClick={handleScroll} data-value="abroad"><i className="fas fa-long-arrow-alt-right"></i> I spent 2 years abroad <i className="fas fa-long-arrow-alt-left"></i></span>
                    </div>
                    <div className="d-flex justify-content-center">
                    <span className="about-scroll" onClick={handleScroll} data-value="pmWork"><i className="fas fa-long-arrow-alt-right"></i> Project Manager for 3 years <i className="fas fa-long-arrow-alt-left"></i></span>
                    </div>
                    <div className="d-flex justify-content-center">
                        <span className="about-scroll" onClick={handleScroll} data-value="programStart"><i className="fas fa-long-arrow-alt-right"></i> Programming since 2020 <i className="fas fa-long-arrow-alt-left"></i></span>
                    </div>
                </div>
                <h4 id="abroad" className="about-experience-heading">Two years abroad</h4>
                <div>
                    <p>First time in 2015 - went to the Hague in the Netherlands. Started as Administrative/IT support voluntary in NGO, then found a job as a Billing assistant.</p>
                    <p>Second time in 2017 - moved to Edinburgh in Scotland. After a brief Bartender/Waiter gig I joined a hotel staff as a Night Auditor in newly opening Adagio Aparthotel.</p>
                </div>
                <h4 id="pmWork" className="about-experience-heading">Worked 3 years as a Project Manager</h4>
                <div>
                    <p>First time in 2016 - right after coming back from the Netherlands found a job as Project Manager in Wunderman company. Great people, interesting job, learnt a lot about online marketing and processes.</p>
                    <p>Second time in 2018-2020 - again after coming back from abroad, this time Scotland. Found most of my old team, gradually gained more experience, responsibilities, becoming Senior Project Manager towards the end.</p>
                </div>
                <h4 id="programStart" className="about-experience-heading">Started with Programming in 2020</h4>
                <div>
                    <p>At the beginning of 2020 I quit my job right when CoronaVirus hit the world. I had a dream of learning Programming all my life, so I took this "opportunity" and started learning.</p>
                    <p>Started with Giraffe Academy video tutorials, Greenfox Academy free 3-weeks course and first freeCodeCamp certificate to learn HTML, CSS, JavaScript and jQuery. Created my first website (not mobile friendly though). Continued with freeCodeCamp certificates and other sources to learn Bootstrap, Sass and ReactJS. Joined my first Hacathon in July to contribute as a Frontend Developer.</p>
                </div>
            </div>
        </section>
    )
}

export default About