import React from "react"
import {Link} from "react-router-dom"
import ApplicationsData from "./ApplicationsData"

const Home = (props) => {

    return (
        <section className="container">
            <div className="d-flex flex-column">
                <div className="d-flex flex-column home-section">
                    <h1>About me</h1>
                    <p>My name is Dan Sprucek and I've been learning programming since March 2020. So far my journey included:</p>
                    <ul>
                        <li>HTML5</li>
                        <li>CSS</li>
                        <li>JavaScript</li>
                        <li>jQuery</li>
                        <li>Sass</li>
                        <li>Bootstrap</li>
                        <li>ReactJS</li>
                    </ul>
                    <h5>Would you like to know more about me?</h5>
                    <Link className="home-links" to="/about">GO TO ABOUT PAGE</Link>
                </div>
                <div className="d-flex flex-column home-section home-midsection">
                    <h1>Portfolio</h1>
                    <p>Here is a sample of my work:</p>
                    <div className="subsection-container">
                        <div className="d-flex flex-column subsection-container-50PER">
                            <a href={ApplicationsData[0].url} target="_blank" rel="noopener noreferrer"><img className="home-portfolio-example" src={`./sources/${ApplicationsData[0].name}.png`} alt={ApplicationsData[0].name} title={ApplicationsData[0].name}></img></a>
                            <h4>{ApplicationsData[0].name}</h4>
                        </div>
                        <div className="d-flex flex-column subsection-container-50PER">
                            <a href={ApplicationsData[1].url} target="_blank" rel="noopener noreferrer"><img className="home-portfolio-example" src={`./sources/${ApplicationsData[1].name}.png`} alt={ApplicationsData[1].name} title={ApplicationsData[1].name}></img></a>
                            <h4>{ApplicationsData[1].name}</h4>
                        </div>
                    </div>
                    <h5>Would you like to know more about my work?</h5>
                    <Link className="home-links" to="/portfolio">GO TO PORTFOLIO PAGE</Link>
                </div>
                <div className="d-flex flex-column home-section">
                    <h1>Contact</h1>
                    <p>Best way to contact me is via e-mail: <a href="mailto:dan.sprucek@gmail.com">dan.sprucek@gmail.com</a> <i className="far fa-copy" data-value="dan.sprucek@gmail.com" onClick={props.copyToClipboard} title="Copy to clipboard"></i></p>
                    <h5>Would you like to know more ways to contact me?</h5>
                    <Link className="home-links" to="/contact">GO TO CONTACT PAGE</Link>
                </div>
            </div>
        </section>
    )
}

export default Home