import React from "react"

const Application = (props) => {
    return (
        <div className="d-flex flex-column mobile-app-container">
            <h3><a href={props.url} target="_blank" rel="noopener noreferrer">{props.name}</a></h3>
            <div className="d-flex justify-content-center mb-2">
                {props.HTML && <span className="badge badge-pill badge-info">HTML5</span>}
                {props.CSS && <span className="badge badge-pill badge-success">CSS</span>}
                {props.Sass && <span className="badge badge-pill badge-success">Sass</span>}
                {props.Bootstrap && <span className="badge badge-pill badge-success">Bootstrap</span>}
                {props.JavaScript && <span className="badge badge-pill badge-danger">JavaScript</span>}
                {props.ReactJS && <span className="badge badge-pill badge-danger">ReactJS</span>}
                {props.Redux && <span className="badge badge-pill badge-danger">Redux</span>}
            </div>
            <div className="d-flex">
                <div>
                    <a href={props.url} target="_blank" rel="noopener noreferrer"><img className={`mobile-app float-right ml-3 ${props.Wide ? "w-100" : "w-50"}`} src={`./sources/${props.name}.png`} alt={props.name} title={props.name}></img></a>
                    <p className="text-justify">{props.description}</p>
                </div>
            </div>
        </div>
    )
}

export default Application