import React, {useState, useEffect} from 'react';
import {Route, Switch, useLocation} from "react-router-dom"
import Navbar from './components/Navbar'
import Home from './components/Home'
import About from './components/About'
import Portfolio from './components/Portfolio'
import Contact from './components/Contact'
import Footer from './components/Footer'

const App = () => {
  const [pageName, setPageName] = useState("");
  let location = useLocation();

  useEffect(() => {
      setPageName(document.getElementsByClassName("active")[0].textContent)
      window.scrollTo(0,0)
  }, [location])

  const copyToClipboard = (event) => {
    let copyText = event.currentTarget.dataset.value
    let textArea = document.createElement("textarea")
    textArea.value = copyText
    textArea.style.top = "0"
    textArea.style.left = "0"
    textArea.style.position = "fixed"
    document.getElementsByClassName("fa-copy")[0].appendChild(textArea)
    textArea.focus()
    textArea.select();
    document.execCommand("copy")
    textArea.remove()
    let confirmation = document.createElement("span")
    confirmation.innerText = "\xa0Copy Successful!"
    confirmation.style.opacity = 0.5
    confirmation.style.zIndex = 2
    confirmation.style.position = "absolute"
    event.currentTarget.appendChild(confirmation)
    setTimeout(() => {
      confirmation.remove()
    }, 1000)
  }

  return (
    <div className="App">
      <Navbar pageName={pageName}/>
      <main>
        <Switch>
          <Route exact path="/" render={(props) => <Home {...props} copyToClipboard={copyToClipboard} />}/>
          <Route path="/about" component={About} />
          <Route path="/portfolio" component={Portfolio} />
          <Route path="/contact" render={(props) => <Contact {...props} copyToClipboard={copyToClipboard} />}/>
        </Switch>
      </main>
      <Footer />
    </div>
  );
}

export default App;
